 
 /* Slider */
 .slick-slider
 {
     position: relative;
     display: block;
     box-sizing: border-box;
   }
 .slick-list
 {
     position: relative;
    display: block;
     overflow: hidden;
  margin: 0px 0px 20px 0px;
 }
 .slick-list:focus
 {
     outline: none;
 }
 .slick-list.dragging
 {
     cursor: pointer;
     cursor: hand;
 }
 .slick-slide
 {
     display: none;
     float: left;  
    height: 100%;
     min-height: 1px;
     max-width: 900px;  
 }
 [dir='rtl'] .slick-slide
 {
     float: right;
 }
  
 .slick-initialized .slick-slide
 {
     display: block;
 }
  
  
  
 /* Arrows */
 .slick-prev,.slick-next{
     font-size: 0; 
     line-height: 0;
     position: absolute;
     top: 50%;
     display: block;
     width: 50px;  
     height: 50px;  
     border-radius: 50%;
     padding: 0;
     transform: translate(0, -50%);
     cursor: pointer;
     border: none;
     outline: none;
   
     background-color: transparent;
     z-index: 20;
     transition: all 0.3s ease .0s;
     
 }
 
 .slick-prev {
     left:  15px;
 }
  
 .slick-prev:before {
  content:"";
  // content: '\00AB';
  background-size: 50px 50px ;
   display: inline-block; // или d-block
   width: 50px; // по размеру background
   height: 50px;
    // background-color: rgb(255, 255, 255);
    background-image: url('../../../assets/img/icons/section-gallery/gallery-left-fast.svg');
}

 
 .slick-next {
     right: 15px;
 }
 .slick-prev:before, .slick-next:before {
   font-size: 40px;
    line-height: 1;
  color: #000000;
  transition: all 0.3s ease .0s;
  
   
  }
  
 .slick-next:before {
  content:"";
  
  background-size: 50px 50px ;
   display: inline-block; // 
   width: 50px; //  
   height: 50px;
   
    background-image: url('../../../assets/img/icons/section-gallery/gallery-right-fast.svg');
 }
 
 .slick-prev:hover:before, .slick-next:hover:before {
   color: rgb(163, 163, 163);
   
 }
 
 /* Dots */
 
 .slick-dots {
   text-align: center;
 }
 .slick-dots li
 {
     position: relative;
     display: inline-block;
     width: 20px;
     height: 20px;
     margin: 0 5px;
     padding: 0;
     cursor: pointer;
 }
 .slick-dots li button
 {
     font-size: 0;
     line-height: 0;
    display: flex;
    align-items: center;
    justify-content: center;
     width: 15px;
     height: 15px;
     padding: 5px;
     cursor: pointer;
     border: 0;
     outline: none;
     background-color: rgb(166, 166, 166);
     border-radius: 50%;
     transition: all 0.3s ease .0s;
     
     
 }
 .slick-dots li button:hover {
   background-color: rgb(82, 82, 82);
 }
  
 .slick-dots li button:focus {
   background-color: rgb(180, 67, 67);
 }
  
 @media screen and (max-width: 600px) {  
  .slick-prev,.slick-next, .slick-next:before, .slick-prev:before {
    width: 30px; //  
    height: 30px;
    background-size: 30px 30px ;
  }
  }
 
 